import { Component, Prop, Vue, Watch } from "vue-property-decorator";


import Address from "@/dto/Address";
import axios from "axios";

@Component
export default class AddressDetails extends Vue {
  @Prop({ required: true })
  address!: Address;
  @Prop({ required: false, default: false })
  edit!: boolean;

  @Prop({ required: false, default: "Address" })
  label!: string;

  selectedState: any = {};
  selectedCity: any = {};
  loadingCities = false;
  statesList = [];
  cityList = [];

  @Watch('edit')
  async onPropertyChanged(value: boolean, oldValue: boolean) {
    if (value === true) {
      this.selectedState = {
        name: this.address.state,
        iso2: this.address.stateCode
      }
      this.selectedCity = {
        name: this.address.city,
      }
      await this.loadcities();

    }
  }
  async mounted() {
    await this.loadStates();

  }

  private updateAddress() {
    this.address.state = this.selectedState.name;
    this.address.stateCode = this.selectedState.iso2;
    this.address.city = this.selectedCity.name;
  }


  cancelEdit() {
    this.$store.commit('setAddSellerPanel', false)
  }



  async loadStates() {
    const response = await axios.get('https://api.countrystatecity.in/v1/countries/IN/states', {
      headers: {
        'X-CSCAPI-KEY': 'dEFGcHBta3E3dWtNWUlsZ09jbXIxSU1xS1BuN0J0Q05BS3Z1UXl6dg=='
      }
    })
    console.log(response)
    this.statesList = response.data.sort((a: any, b: any) => a.name.localeCompare(b.name))
  }
  async loadcities() {
    try {
      this.loadingCities = true;
      const stateCode = this.selectedState.iso2;
      console.log(this.selectedState)
      const response = await axios.get(`https://api.countrystatecity.in/v1/countries/IN/states/${stateCode}/cities`, {
        headers: {
          'X-CSCAPI-KEY': 'dEFGcHBta3E3dWtNWUlsZ09jbXIxSU1xS1BuN0J0Q05BS3Z1UXl6dg=='
        }
      })
      this.cityList = response.data.sort((a: any, b: any) => a.name.localeCompare(b.name))
      this.address.state = this.selectedState.name;
      this.address.stateCode = this.selectedState.iso2;

    } catch (error) {

    } finally {
      this.loadingCities = false;
    }

  }
  async loadcitiesOnStateChange() {
    try {
      this.loadingCities = true
      this.selectedCity = {};
      const stateCode = this.selectedState.iso2;
      console.log(this.selectedState)
      const response = await axios.get(`https://api.countrystatecity.in/v1/countries/IN/states/${stateCode}/cities`, {
        headers: {
          'X-CSCAPI-KEY': 'dEFGcHBta3E3dWtNWUlsZ09jbXIxSU1xS1BuN0J0Q05BS3Z1UXl6dg=='
        }
      })
      this.cityList = response.data.sort((a: any, b: any) => a.name.localeCompare(b.name))
      this.address.state = this.selectedState.name;
      this.address.stateCode = this.selectedState.iso2;

    } catch (error) {

    } finally {
      this.loadingCities = false;
    }

  }

}
