import { Component, Prop, Vue } from "vue-property-decorator";

import store from '@/store';
import { firestore, storage, auth } from '@/firebaseInit';
import { SellerInfo } from '@/dto/SellerInfo';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';
import { UtilityService } from '@/services/UtilityService';
import AddressDetails from '@/components/Address/AddressDetails.vue';
@Component(
  {
    components: {
      AddressDetails
    }
  })
export default class Seller extends Vue {
  @Prop({ required: false, default: function () { return new SellerInfo() } })
  vendor!: SellerInfo;
  @Prop({ required: false, default: false })
  edit!: boolean;

  selectedImageUrl = "";
  selectedImage: any = null;

  loading = false;
  label = "Add Seller";
  uploadRules = [
    (value: any) => !!value || 'logo is required',
    (value: any) =>
      !value ||
      value.size < 512000 ||
      'logo must be less than 500kb',
  ]
  async mounted() {

    this.label = this.edit ? "Edit Seller" : "Add Seller";
    if (this.edit) {
      this.selectedImageUrl = this.vendor.logoUrl;
      this.uploadRules = [
        (value: any) =>
          !value ||
          value.size < 512000 ||
          'logo must be less than 500kb',
      ]
    }
  }
  async validateForm() {
    if ((this.$refs.sellerForm as Vue & { validate: () => boolean }).validate()) {
      await this.processRequest();
    }
  }
  resetForm() {
    this.vendor = new SellerInfo();
    this.selectedImage = null;
    (this.$refs.sellerForm as Vue & { reset: () => boolean }).reset();
  }
  previewImage() {
    if (this.selectedImage)
      this.selectedImageUrl = URL.createObjectURL(this.selectedImage)
    else
      this.selectedImageUrl = ''
  }
  clearImages() {
    this.selectedImageUrl = ""
  }
  async processRequest() {
    try {
      if (this.edit)
        await this.updateSeller();
      else {
        await this.addSeller();

      }
    } catch (error) {

    }
  }
  async addSeller() {
    if (this.selectedImage != null) {
      try {
        this.loading = true;
        this.vendor.id = UtilityService.convertToKebabCase(this.vendor.name) as string;
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };
        const resp = await storage.ref('vendor-logos/' + this.vendor.id + '-logo.' + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.vendor.logoUrl = url;
        this.vendor.id = UtilityService.convertToKebabCase(this.vendor.name) as string;
        const dateNow = new Date();

        const auditfield: AuditFields = {
          createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
          createdOn: dateNow.getTime()
        }
        console.log(url);
        this.vendor.auditTrail = auditfield;
        const dbResp = await FirestoreService.addDocumentWithId('vendors', this.vendor.id, this.vendor, SellerInfo.converter);
        Vue.toasted.show('Vendor Added Successfully', { icon: 'face', type: 'success' });

        this.resetForm();
      } catch (error) {
        console.log(error)
        Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

      } finally {
        this.loading = false;
      }
    }
  }


  cancelEdit() {
    this.$store.commit('setCloseSeller', false)
  }
  async updateSeller() {

    try {
      this.loading = true;
      this.vendor.id = UtilityService.convertToKebabCase(this.vendor.name) as string;
      if (this.selectedImage != null) {
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };
        const resp = await storage.ref('vendor-logos/' + this.vendor.id + '-logo' + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.vendor.logoUrl = url;
        console.log(this.vendor.logoUrl);

      }
      this.vendor.id = UtilityService.convertToKebabCase(this.vendor.name) as string;
      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdOn: this.vendor.auditTrail.createdOn,
        createdBy: this.vendor.auditTrail.createdBy,
        updatedBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        updatedOn: dateNow.getTime()
      }
      this.vendor.auditTrail = auditfield;
      const dbResp = await FirestoreService.setDocument('vendors', this.vendor.id, this.vendor, SellerInfo.converter);
      Vue.toasted.show('Vendor Updated Successfully', { icon: 'face', type: 'success' });
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setCloseSeller', false)
    }
  }


  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }




}
