import { v4 as uuidv4 } from 'uuid';

export default class Address {
    country: string;
    state: string;
    city: string;
    stateCode: string;
    cityCode: string;
    streetAddress: string;
    stateGSTCode: string;
    pinCode: string;
    id: string;
    name: string;
    mobileNumber: string;
    constructor() {
        this.country = 'India';
        this.state = '';
        this.city = '';
        this.stateCode = '';
        this.cityCode = '';
        this.streetAddress = '';
        this.pinCode = '';
        this.id = uuidv4();
        this.stateGSTCode = '';
        this.name = '';
        this.mobileNumber = '';
    }
}
